<template>
<section class="home m-0 p-0" v-if="info !==null">
  <Header v-bind:firstname="info.info.firstName" v-bind:fullname="fullName" v-bind:headersName="info.header_name"></Header>
  <Home v-bind:about="info.introduction" v-bind:info="info.info"/>
  <About v-if="info.header.about" v-bind:info="info.info" v-bind:about="info.introduction"></About>
  <Skill v-if="info.header.skills" v-bind:skillsummary="info.skill" v-bind:skills="info.skill.skills"></Skill>
  <Experience v-if="info.header.experience" v-bind:experience="info.experience" v-bind:about="info.introduction" />
  <Certification v-if="info.header.certification" v-bind:certification="info.certification"></Certification>
  <Contact v-if="info.header.contact" v-bind:successMessage="info.successMailMessage" v-bind:errorMessage="info.errorMailMessage" v-bind:info="info.info" v-bind:link="info.website"></Contact>
</section>
</template>

<script>
import Header from './../header/nav.vue'
import Home from './home.vue'
import About from './about.vue'
import Skill from './skills.vue'
import Experience from './experience.vue'
import Certification from './certification.vue'
import Contact from './contact.vue';
const axios = require('axios');
const util = require('./../Util/util.js');
export default {
  name: 'Main',
  myOption: 'hello!',
  components: {
    Header,
    Home,
    About,
    Skill,
    Experience,
    Certification,
    Contact
  },
  data: () => {
    return {
      info: null,
      fullName: null
    }
  },
  mounted() {
    window.addEventListener('scroll', function() {
        if(util.elementInViewPort(document.getElementById('home-section'))){
          util.active('home-section')
        }

        if(util.elementInViewPort(document.getElementById('about-section'))){
          util.active('about-section')
        }

        if(util.elementInViewPort(document.getElementById('skills-section'))){
          util.active('skills-section')
        }

        if(util.elementInViewPort(document.getElementById('resume-section'))){
          util.active('resume-section')
        }

        if(util.elementInViewPort(document.getElementById('services-section'))){
          util.active('services-section')
        }

        if(util.elementInViewPort(document.getElementById('contact-section'))){
          util.active('contact-section')
        }
    });
    let url = process.env.VUE_APP_PROJECT_ROOT + process.env.VUE_APP_DATA_URI;
    axios
      .get(url)
      .then(response => {
        if (response.status === 200) {
          this.info = response.data;
          this.fullName = this.info.info.firstName + " " + this.info.info.lastName
          // alert(JSON.stringify(this.info.info))
        }
      })
  }
}
</script>

<style lang="scss" >
html {
    // overflow-x: hidden;
}
</style>
