<template>
<section class="certification" id="services-section">
  <div class="container">
    <div class="row justify-content-center py-5 " data-aos="fade-up" data-aos-duration="1000" data-aos-anchor-placement="center-bottom">
      <div class="col-md-12 heading-section text-center ">
        <h1 class="big big-2">certification</h1>
        <h2 class="mb-4">Certifications</h2>
      </div>
    </div>
    <div class="row p-4 d-flex align-items-center justify-content-center">
      <div class="col-md-4 text-center d-flex" v-for="(cert, index) in certification" v-bind:key="index" data-aos="zoom-in-up" data-aos-duration="1000" data-aos-anchor-placement="center-bottom">
        <a v-if="!cert.iframe" v-bind:href="cert.url" class="services-1">
          <div class="img-about img d-flex align-items-stretch  d-flex align-items-center justify-content-center">
            <img v-bind:src="root+cert.badgeurl" class="rotate-continous" v-bind:alt="cert.badgealt">
          </div>
          <div class="desc">
            <h3 class="mb-5">{{cert.display}}</h3>
          </div>
        </a>
        <div class="col-md-4 text-center d-flex" data-aos="zoom-in-up" data-aos-duration="1000" data-aos-anchor-placement="center-bottom" v-if="cert.iframe">
          <div class="services-1">
            <div class="desc" v-bind:id="'cert'+index">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import Certification from './../model/certification.js';
export default {
  name: 'Certification',
  props: {
    certification: {
      type: Array,
      default: new Array()[new Certification()]
    },
  },
  data: () => {
    return {
      root: ''
    }
  },
  mounted() {
    this.root = process.env.VUE_APP_PROJECT_ROOT;
    this.certification.forEach((cert, index) => {
      if (cert.iframe === true) {
        let id = 'cert' + index;
        document.getElementById(id).innerHTML = cert.iframeValue;
      }
    })
  }
}
</script>

