import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import Main from './components/home/main.vue'
import Error from './components/error/error.vue'
import './../node_modules/bootstrap/dist/css/bootstrap.min.css'
import './../node_modules/aos/dist/aos.css'
const _aos = require('./../node_modules/aos/dist/aos.js');
import 'bootstrap'
try{
Vue.config.productionTip = false
Vue.use(VueRouter)
_aos.init();
const routes = [
  { path: '/home', alias: '/', component: Main },
  { path: '/error', component: Error }
]

// Create the router instance and pass the `routes` option
const router = new VueRouter({
  routes
})

new Vue({
  el: '#app',
  router,
  myOption: 'hello!',
  render: h => h(App)
})
}
catch(err){
  console.log(err);
  window.location.href= window.location.protocol+'//'+window.location.hostname+(window.location.port === "" || window.location.port ===undefined ? '':':'+window.location.port)+'/error.html';
}
