<template>
<section class="header">
  <nav class="navbar navbar-expand-lg navbar-dark" id="cv-navbar">
    <div class="container breakout">
      <a class="navbar-brand d-none d-md-block d-lg-block d-xl-block" href="index.html">{{fullname}}</a>
      <a class="navbar-brand  d-md-none d-sm-block d-lg-none" href="index.html">{{firstname}}</a>
      <a class="navbar-toggler d-sm-block d-md-block d-lg-none btn btn-primary py-2 px-2" v-on:click="openMenu()" type="button" data-toggle="collapse" data-target="#nav-expand" aria-controls="nav-expand" aria-expanded="false" aria-label="Toggle navigation">
        <span class="material-icons button_text">home</span> <span class="button_text"> Menu</span>
      </a>
      <div class="collapse navbar-collapse" id="nav-expand">
        <ul class="navbar-nav nav nav-box">
          <li class="nav-item" v-for="(header, index) in headersName" v-bind:key="index" v-bind:index="index" v-bind:id="'nav_'+index" >
            <a v-if="header.display" v-bind:href="header.id" v-bind:class="[index === 0 ? 'active' : '', 'nav-link']"  v-on:click="makeActive(index)">
              <span class="material-icons button_text">chevron_right</span>
              <span class="button_text"> {{header.name}}</span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</section>
</template>

<script>

export default {
  name: 'Header',
  props: {
    headersName: {
      type: Array,
      default: new Array()
    },
    firstname: {
      type: String,
      default: "Name"
    },
    fullname: {
      type: String,
      default: "Name"
    }
  },
  methods:{
    makeActive: function(index){
      this.headersName.forEach((value,index) => {
        document.getElementById('nav_'+(index.toString())).classList.remove('active');
      })
      document.getElementById('nav_'+(index.toString())).classList.add('active');
      document.getElementById('nav-expand').classList.remove('show');

    },
    openMenu: function(){
      // document.getElementById('nav-expand').classList.add('show');
    }
  },
  mounted(){
      this.headersName.data.forEach((value,index) => {
      document.getElementById('nav_'+(index.toString())).classList.remove('active');
    })
    document.querySelector('a[href="#home-section"]').parentElement.classList.add('active');
  }
}
</script>

