const elementInViewPort = (el) => {
  var top = el.offsetTop;
    var left = el.offsetLeft;
    var width = el.offsetWidth;
    var height = el.offsetHeight;

    while(el.offsetParent) {
      el = el.offsetParent;
      top += el.offsetTop;
      left += el.offsetLeft;
    }

    return (
      top < (window.pageYOffset + 72) &&
      left < (window.pageXOffset + window.innerWidth) &&
      (top + height) > window.pageYOffset &&
      (left + width) > window.pageXOffset
    );
}

const active= (id) => {
  document.querySelector('a[href="#'+id+'"]').parentElement.parentElement.childNodes.forEach((element)=>{
    if(element && element.firstChild && element.firstChild.classList){
    element.firstChild.classList.remove('active');
  }
  })
  document.querySelector('a[href="#'+id+'"]').classList.add('active');
}

exports.elementInViewPort = elementInViewPort;
exports.active = active;
