<template>
<section class="skill-section" id="skills-section">
  <div class="container" data-aos="fade-up" data-aos-duration="1000">
    <div class="row justify-content-center pb-5">
      <div class="col-md-12 heading-section text-center ">
        <h1 class="big big-2">Skills</h1>
        <h2 class="mb-4">My Skills</h2>
        <p>{{skillsummary.shortSummary}}</p>
      </div>
    </div>
    <div id="carouselExampleControls" data-aos="fade-right" data-aos-duration="1000" class="carousel slide" data-ride="carousel">
      <div class="carousel-inner">
        <div v-bind:class="[{ 'active': index === 0 }, 'carousel-item']" v-for="(sskill, index) in slideSkill" v-bind:key="index">
          <div class="row">
            <ProgressSkill v-for="(skill, index) in sskill" v-bind:key="index" v-bind:skills="skill"></ProgressSkill>
          </div>
        </div>
      </div>

      <a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </a>
      <a class="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </a>
    </div>
  </div>
</section>
</template>

<script>
import Skill from './../model/skillsummary.js';
import Skills from './../model/skill.js';
import ProgressSkill from './partial/progressSkill.vue';
export default {
  name: 'Skill',
  components: {
    ProgressSkill
  },
  data: () => {
    return {
      slideSkill: new Array(),
      first: true
    }
  },
  props: {
    skillsummary: {
      type: Object,
      default: new Skill()
    },
    skills: {
      type: Array,
      default: new Array()[new Skills()]
    }
  },
  methods: {
  },
  mounted() {
    while (this.skills.length > 0) {
      let chunk = this.skills.splice(0, this.skillsummary.no_skill_per_slide);
      this.slideSkill.push(chunk);
    }
  }
}
</script>

