import Link from './../model/link.js';
var service_id = "default_service";
var template_id = "template_nkf9ddvt";
export default {
  data: function() {
    return {
      errors: [],
      fieldRequired: 'Field cannot be leave blank',
      emailPatter: 'Enter a valid email address',
      Personname: null,
      email: null,
