      default: new Info()
    },
    link: {
      type: Array,
      default: new Array()[new Link()]
    }
  },
  methods: {
    sendEmail: function(e) {
      try {
        emailjs.sendForm(service_id, template_id, e.target, 'user_uezb7Jg20HaBeIVZJkLcq')
          .then(() => {
            this.success = true;
          }, () => {
            this.faliure = true;
          });
      } catch (e) {
        this.faliure = true;
      }
    }
  }
}
