<template>
<div class="resume-wrap flip-box" v-bind:id="'flip-box'+index+suffix" data-aos="fade-right" data-aos-duration="1000" data-aos-anchor-placement="center-bottom">

  <div class="flip-box-inner" v-bind:id="'flip-box-inner'+index+suffix">
    <div class="flip-box-front">
      <span class="date">{{block.startYear}}-{{block.endYear}}</span>
      <h2>{{block.title}}</h2>
      <span class="position">{{block.subtitle}}</span>
      <p v-bind:class="{ 'mt-4': index===0 }" v-for="(about, index) in block.intro" v-bind:key="index">{{about}}</p>
      <span class="date mb-1" v-if="!block.more && block.skill">Skills</span>
      <div class="skill " v-if="!block.more && block.skill">
        <span v-for="(skill, index) in block.skillSection" v-bind:key="index" class="badge badge-pill badge-info p-2">{{skill}}</span>
      </div>
      <button v-if="block.more" class="resBtn" v-on:click="seeMore(index+suffix)">{{block.primaryCTC}}</button>
    </div>
    <div class="flip-box-back" v-if="block.more && seemore">
      <div v-for="(back, index) in block.backSection" v-bind:key="index">
        <span class="date">{{back.title}}</span>
        <p v-bind:class="{ 'mt-2': index===0 }" v-for="(intro, index) in back.intro" v-bind:key="index">{{intro}}</p>
      </div>
      <span class="date mb-1" v-if="block.skill">Skills</span>
      <div class="skill " v-if="block.skill">
        <span v-for="(skill, index) in block.skillSection" v-bind:key="index" class="badge badge-pill badge-info p-2">{{skill}}</span>
      </div>
      <button class="resBtn back" v-on:click="back(index+suffix)">Back</button>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'ExpBlock',
  data: () => {
    return {
      seemore: false
    }
  },
  props: {
    block: {
      type: Object
    },
    index: {
      type: String,
      default: "0"
    },
    suffix: {
      type: String,
      default: ""
    }
  },
  methods: {
    seeMore: function(no) {
      this.seemore = true;
      document.getElementById('flip-box' + no).classList.add('transform')
      document.getElementById('flip-box-inner' + no).classList.add('transform')
    },
    back: function(no) {
      document.getElementById('flip-box' + no).classList.remove('transform')
      document.getElementById('flip-box-inner' + no).classList.remove('transform')
      this.seemore = false;
    }
  }

}
</script>

<style lang="scss" scoped>
.resume-wrap {
    .resBtn {
        color: $primary;
        border: none;
        background-color: transparent;
        text-align: right;
        width: 100%;
        text-decoration: underline;
    }
    width: 100%;
    margin-bottom: 30px;
    background: rgba(255,255,255,.1);
    padding: 30px;
    @include border-radius(5px);
    .date {
        font-weight: 900;
        font-size: 26px;
        color: $primary;
    }
    h2 {
        font-size: 26px;
    }
    .position {
        font-size: 12px;
        font-weight: 600;
        letter-spacing: 3px;
        text-transform: uppercase;
    }
}
div.skill {
    margin-top: 8px;
}
.resBtn.back {
    position: sticky !important;
    bottom: 0;
}
</style>
