<template>
<section class="resume" id="resume-section">
  <div class="container">
    <div class="row justify-content-center pb-5" data-aos="zoom-in-up" data-aos-duration="1000" data-aos-anchor-placement="center-bottom">
      <div class="col-md-10 heading-section text-center ">
        <h1 class="big big-2">Experience</h1>
        <h2 class="mb-4">Experience</h2>
        <p>{{experience.shortSummary}}</p>
      </div>
    </div>
    <div class="row">
      <!-- Only for Desktop view -->
      <div class="col-md-12 col-lg-6 col-12 d-none d-lg-block">
        <ExpBlock v-for="(block, index) in firstHalf" v-bind:key="index" v-bind:block="block" v-bind:index="index" v-bind:suffix="'fh'"></ExpBlock>
      </div>
      <div class="col-md-12 col-lg-6 col-12 d-none d-lg-block" >
        <ExpBlock v-for="(block, index) in secondHalf" v-bind:key="index" v-bind:block="block" v-bind:index="index" v-bind:suffix="'sh'"></ExpBlock>
      </div>

      <!-- Only for Tablet and mobile view -->
      <div class="col-md-12 col-lg-6 col-12 d-lg-none d-xl-none" v-for="(block, index) in experience.exp" v-bind:key="index">
        <ExpBlock v-bind:block="block" v-bind:index="index" v-bind:suffix="'md'"></ExpBlock>
      </div>
    </div>
    <div class="row justify-content-center mt-5" data-aos="fade-up" data-aos-duration="1000" data-aos-anchor-placement="center-bottom">
      <div class="col-md-6 text-center flip-boxd">
        <p><a v-bind:href="about.download_cv" v-bind:download="about.downloadFileName" class="btn btn-primary py-3 px-4 m-2"><span class="material-icons">arrow_downward</span> <span class="button_text"> {{about.cv_ctc_name}}</span></a></p>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import Experience from './../model/experience.js';
import About from './../model/about.js';
import ExpBlock from './partial/experienceBlock.vue';
export default {
  e1: '#resume-section',
  name: 'Experience',
  components: {
    ExpBlock
  },
  data: () => {
    return {
      seemore: false,
      firstHalf: new Array(),
      secondHalf: new Array()
    }
  },
  props: {
    experience: {
      type: Object,
      default: new Experience()
    },
    about: {
      type: Object,
      default: new About()
    }
  },
  methods: {
    seeMore: function(no) {
      this.seemore = true;
      let flipbox = document.querySelectorAll('#flip-box' + no);
      [].forEach.call(flipbox, function(flipbox) {
        flipbox.classList.add('transform')
      });
      let flipboxInner = document.querySelectorAll('#flip-box-inner' + no);
      [].forEach.call(flipboxInner, function(flipboxInner) {
        flipboxInner.classList.add('transform')
      });
    },
    back: function(no) {
      let flipbox = document.querySelectorAll('#flip-box' + no);
      [].forEach.call(flipbox, function(flipbox) {
        flipbox.classList.remove('transform')
      });
      let flipboxInner = document.querySelectorAll('#flip-box-inner' + no);
      [].forEach.call(flipboxInner, function(flipboxInner) {
        flipboxInner.classList.remove('transform')
      });
      this.seemore = false;
    }
  },
  mounted() {
    this.experience.exp.forEach((element, index) => {
      if (index % 2 === 0) {
        this.firstHalf.push(element);
      }
      else{
        this.secondHalf.push(element);
      }
    })
  }
}
</script>

